  transform: scale(1.1);
}

.link--text:before {
  content: "";
  background-color: #2060f6;
  border-radius: 2px;
  width: 100%;
  height: 2px;
  transition: transform .3s;
  position: absolute;
  bottom: -13px;
  transform: scale(0);
}

.link--text:hover:before {
  transform: scale(1);
}

.link__icon svg {
  fill: #fff;
  transition: fill .3s;
}

